.img-picker-container {
    width:300px;
    height: 83px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    background: #5c88d1;
    border-radius: 8%;
    margin-top: 20px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none
}

.img-picker-container img {
    height: 65px;
    width: 65px;
    margin:5px;
}
.imgs-container{
    margin: 0 15px;
}
.arrow{
    cursor: pointer;
    color: lightsteelblue;
}
.arrow:hover{
color:#245fc1;
}


